import { useRef } from "react";

const Params = ({page}) => {

    const main = useRef();
    // const analytic = useRef();
    const parabole = useRef();
    const analytic_extended = useRef();

    return page(
    <div>
        <h1 ref={main} onClick={page.index} className="main-menu">  </h1>
        <ul className="list">
            <li></li>
            {/* <li onClick={page.scroll(analytic)}></li> */}
            <li onClick={page.scroll(analytic_extended)}> (extended)</li>
            <li></li>
            <li onClick={page.scroll(parabole)}> </li>
            <li></li>
            <li></li>
            <li></li>
        </ul>
           .     .
        <h2 onClick={page.scroll(main)} ref={analytic_extended} className="main-menu"> (extended)</h2>
        <div className="frame">
            <h3> :</h3>
                <Task
                    text={`   \\(x\\)  <br>\\(
                        <center>F(x, a)
                    \\)<br>    \\(a\\).`}
                />
            <h3>:</h3>
            <ol>
                <li>
                     {`\\(F(x, a) \\LR
                        \\union{
                            G_1(x,a)~~~(1) \\\\
                            G_2(x,a)~~~(2) \\\\
                            \\ldots \\\\
                            G_n(x, a)~~~(n)
                        }
                    \\)`}.
                </li>
                <li>
                    <div>     {`\\(a\\)`}    {page.tex('G_i(x,a)')}.</div>
                    <div>{`\\(G_i(x,a)\\)`}      :</div>
                    <ul>
                        <li>
                            <div>{`\\( \\cases{x = x(a) \\\\ a \\in A} \\).`}</div>
                            <div> :</div>
                            <div className="flex line">
                                <div>{`\\((i):~~\\)`}</div> <div>
                                    <div>1   {`\\(a \\in A\\)`}</div>
                                    <div>   {`\\(a \\not\\in A\\)`}</div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div>{`\\( \\cases{x \\in X \\\\ a \\in A} ~~~,\\)  \\(~~X ~- \\)  .`}</div>
                            <div> :</div>
                            <div>
                                <div className="flex line">
                                    <div>{`\\((i):~~\\)`}</div>
                                    <div>
                                        <div>   {`\\(a \\in A\\)`}</div>
                                        <div>   {`\\(a \\not\\in A\\)`}</div>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div>{`\\(\\cases{\\cases{\\alpha x^2 + \\beta x + \\gamma = 0 \\\\ a\\in A}~~~(i.1) \\\\ f(x,a) \\neq 0}\\),  \\(f(x,a)\\)   .`}</div>
                            <div> :</div>
                            <div className="flex line">
                                <div>{`\\((i.1):~~\\)`}</div>
                                <div>
                                    <div>
                                         {`\\(\\alpha = 0:~~\\cases{
                                        \\beta x + \\gamma = 0 \\\\
                                        a \\in A \\\\
                                        \\alpha = 0
                                        }\\)`}
                                    </div>
                                    <div>
                                         {`\\(\\alpha \\neq 0:~~\\alpha x^2 + \\beta x + \\gamma = 0 ~-\\)`}  , {`\\(D=D(a)\\)`}
                                    </div>
                                    <div>
                                        2   {`\\(\\cases{D>0 \\\\ a \\in A \\\\ \\alpha \\neq 0}\\)`}
                                    </div>
                                    <div>
                                        1   {`\\(\\cases{D=0 \\\\ a \\in A\\\\ \\alpha \\neq 0}\\)`}
                                    </div>
                                    <div>
                                        0   {`\\(\\cases{D<0 \\\\ a \\in A\\\\ \\alpha \\neq 0}\\)`}
                                    </div>
                                    <div>
                                         {`\\(a\\)`},   {`\\(f(x,a)=0\\)`}:
                                    </div>
                                    <div>{`\\(\\cases{\\alpha x^2 + \\beta x + \\gamma = 0 \\\\ a\\in A \\\\ f(x,a)=0}\\)`}</div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div>{`\\(G(x, a)\\)`}</div>
                                 {`\\(a\\)`}  .
                        </li>
                    </ul>
                </li>
                <li>
                    <div>  , :</div>
                    <div className="line">
                        <div>  ({`\\(i\\)`})  ({`\\(j\\)`}):</div>
                        <div>{page.tex('\\cases{G_i(x,a) \\\\ G_j(x,a)}')}</div>
                    </div>
                </li>
                <li>
                       (1), (2),..., ({`\\(n\\)`})
                      {`\\(a\\)`}   .   .
                </li>
                <li>
                         {`\\(a\\)`}    .
                </li>
            </ol>
        </div>
        {/* <h2 onClick={page.scroll(main)} ref={analytic} className="main-menu"></h2>
        <div className="frame">
            <h3> :</h3>
                <Task
                    text={`   \\(x\\)  <br>\\(
                        <center>F(x, a)
                    \\)<br>    \\(a\\).`}
                />
            <h3>:</h3>
            <ol>
                <li>
                     {`\\(F(x, a) \\LR
                        \\union{
                            G_1(x,a)~~~(1) \\\\
                            G_2(x,a)~~~(2) \\\\
                            \\ldots \\\\
                            G_n(x, a)~~~(n)
                        }
                    \\)`}.
                </li>
                <li>
                    <div>       {page.tex('G_i(x,a)')}.</div>
                    <div>{`\\(G_i(x,a)\\)`}      :</div>
                    <ul>
                        <li>
                            <div>{`\\( \\cases{x = x_i(a) \\\\ a \\in A} \\).`}</div>
                            <div> :</div>
                            <div>
                                {`\\((i):~~x_i = x_i(a)\\)    \\(a \\in A\\).`}
                            </div>
                        </li>
                        <li>
                            <div>{`\\( \\cases{x \\in T \\\\ a \\in A} ~~~,\\)  \\(~~T ~- \\)  .`}</div>
                            <div> :</div>
                            <div>
                                {`\\((i):~~\\)  \\(a \\in A\\)  .`}
                            </div>
                        </li>
                        <li>
                            <div>{`\\(\\cases{\\cases{x^2 + px + q = 0 \\\\ a\\in A}~~~(i.1) \\\\ f(x,a) \\neq 0}\\),  \\(f(x,a)\\)   .`}</div>
                            <div> :</div>
                            <div>
                                {`\\((i):~~ x_{i_1,i_2}\\)   \\((i.1)\\).`}
                            </div>
                            <div>  {page.tex('\\cases{D \\geq 0 \\\\ a\\in A}')}.</div>
                            <div>  {page.tex('\\cases{D = 0 \\\\ a\\in A}')}</div>
                            <div>  {page.tex('a')},   {page.tex('f(x,a) = 0')}</div>
                            <div>{page.tex('\\cases{x^2 + px + q = 0 \\\\ a\\in A \\\\ f(x,a)=0}')}</div>
                        </li>
                    </ul>
                </li>
                <li>
                    <div>   {page.tex('x_i')}:</div>
                    <div>{`\\(x_i = x_j\\)  \\(a\\),     \\((i)\\)  \\((j)\\):`}</div>
                    <div>{page.tex('\\cases{G_i(x,a) \\\\ G_j(x,a)}')}</div>
                </li>
                <li>            {page.tex('a')}.</li>
            </ol>
        </div> */}
        <h2 ref={parabole} onClick={page.scroll(main)} className="main-menu"> </h2>
        <div className="frame">
            <h3> :</h3>
                <Task
                    text = {`     <br>\\(
                        <center>ax^2 + bx + c \\vee 0
                    \\)<br>    ,   \\(x_i\\).`}
                />
            <h3>:</h3>
            <ol>
                <li>  (1): {'\\(a = 0\\)'}</li>
                <li>  (2): {'\\(a \\neq 0\\)'}</li>
                <li>
                    <div>:</div>
                        <ul>
                            <li>{'\\(f(x) = ax^2 + bx+c ~-\\) '}</li>
                            <li>{'\\(D = b^2 - 4ac\\)'}</li>
                            <li>{' \\(x_ = \\frac{-b}{2a}\\)'}</li>
                            <li>{'\\(f(x_i) = ax_i^2+bx_i+c\\)'}</li>
                        </ul>
                </li>
                <li>
                    <div> :</div>
                    <div>(2.1) {'\\(D<0;~~\\)'}   {'\\(a\\)'}   </div>
                    <div>(2.2) {'\\(D=0;~~\\)'}   {'\\(x_,~a\\)'}   </div>
                    <div>
                        (2.3) {'\\(D>0\\)'}
                        <div style={{marginLeft:'30px'}}>
                            (2.3.1) {'\\(x_ \\vee x_i;~~\\)'}   {'\\(a\\cdot f(x_i)\\)'}   
                        </div>
                    </div>
                </li>
                <li>
                    <div>    :</div>
                    <ul>
                        <li>
                            <div>{'\\(a>0~~-\\)   '}</div>
                            <div>{'\\(a<0~~-\\)   '}</div>
                        </li>
                        <li>
                            <div>{'\\(D<0~~-\\)        '}</div>
                            <div>{'\\(D=0~~-\\)      '}</div>
                            <div>{'\\(D>0~~-\\)    '}</div>
                        </li>
                        <li>
                            <div>{'\\(a\\cdot f(x_i)<0~~-\\)    \\(x_i\\)'}</div>
                            <div>{'\\(a\\cdot f(x_i)>0~~-\\)    \\(x_i\\)'}</div>
                            <div>{'\\(a\\cdot f(x_i)=0~~-\\)     \\(x_i\\)'}</div>
                        </li>
                    </ul>
                </li>
            </ol>
        </div>
    </div>
)};

const Task = ({text}) => (
    <div className="param-task">
        {
            text.split('<br>').map(str => 
            <div style={{display:'flex', justifyContent:str.indexOf('<center>')===-1?'start':'center'}}>
                <div style={{width:'fit-content'}}>{str.replace('<center>','')}</div>
            </div>
            )
        }
    </div>
);

export default Params;